import { useState, useEffect } from 'react';
import {Header} from './components/Header'
import {Sidebar} from './components/Sidebar'
import {MainContent} from './components/MainContent'

function App() {

  const [animeList, setAnimeList] = useState([]);
  const [topAnime, setTopAnime] = useState([]);
  const [search, setSearch] = useState('');

  const getAnimeTop = async () =>{
      const getData = await fetch('https://api.jikan.moe/v3/top/anime/1/bypopularity')
      const data = await getData.json()
      setTopAnime(data.top.slice(0,10));
      
  }

  const handleSearch = (e) =>{
      e.preventDefault();
      fetchAnime(search);
  }

  const fetchAnime = async(query) =>{
      const getData = await fetch(`https://api.jikan.moe/v3/search/anime?q=${query}&order_by=title&sort=asc&limit=10`)
      const data = await getData.json()
      setAnimeList(data.results)
      console.log(data.results[0].title)
  }

  useEffect(()=> {
    getAnimeTop();

    
  },[])
  
  return (
    <div className= 'App'>
      <Header/>
      <div className="content-wrap">
        <Sidebar
            topAnime={topAnime}/>
        <MainContent
            handleSearch={handleSearch}
            search={search}
            setSearch={setSearch}
            animeList={animeList}
        />
      </div>
    </div>
  );
}

export default App;
